<template>
  <div class="animated fadeIn">
    <b-form-group>
      <b-form-input
        v-model="inputValue"
        type="text"
        class="form-control-success"
        placeholder="关键词搜索"
      ></b-form-input>
    </b-form-group>
    <b-row>
      <template v-for="(item,index) in dashboardList">
        <b-col
          sm="6"
          md="4"
          :key="index"
          v-if="item.header.includes(inputValue)||item.body.includes(inputValue)||item.right.some(x=>x.value.includes(inputValue))">
          <b-card :class="item.class">
            <div slot="header">
              <span v-html="item.header"></span>
              <b-badge v-for="(x,i) in item.right" :key="i" :variant="x.color" class="float-right badge-left cursor" @click="inputSelect(x.value)">{{x.value}}</b-badge>
            </div>
            <div class="cursor" @click="openLink(item)" v-html="`<a href='${item.link}' target='_blank'>${item.body}</a>`"></div>
          </b-card>
        </b-col>
      </template>
    </b-row>
  </div>
</template>

<script>
import { dashboardList } from "@/shared/TXT.js";
export default {
  name: "dashboard",
  data() {
    return {
      dashboardList,
      inputValue: ""
    };
  },
  created() {
    this.listInit();
  },
  methods: {
    // 基础列表数据处理
    listInit() {
      this.dashboardList = this.$deepClone(dashboardList);
      this.dashboardList.forEach((item) => {
        item.link = item.body;
        item.right.forEach((x)=>{
          switch(x.value) {
            case "vue":
              x.color = "success";
              break;
            case "app":
              x.color = "success";
              break;
            case "ts":
              x.color = "dark";
              break;
            case "react":
              x.color = "primary";
              break;
            case "ios":
              x.color = "warning";
              break;
            case "js":
              x.color = "danger";
              break;
            case "css":
              x.color = "primary";
              break;
            case "git":
              x.color = "info";
              break;
            case "H5":
              x.color = "warning";
              break;
            default:
              x.color = "secondary";
          }
        })
        item.class = "card-accent-" + item.right[0].color;
      });
    },
    // 打开对应链接
    openLink(item) {
      window.open(item.link);
    },
    // 点击搜索类型
    inputSelect(value) {
      this.inputValue = value;
    }
  },
  watch: {
    inputValue() {
      this.listInit();
      this.dashboardList.forEach(item => {
        if (item.header.includes(this.inputValue)) {
          item.header = item.header.replace(
            new RegExp(this.inputValue, "gi"),
            `<span class="primary_title">${this.inputValue}</span>`
          );
        }
        if (item.body.includes(this.inputValue)) {
          item.body = item.body.replace(
            new RegExp(this.inputValue, "gi"),
            `<span class="primary_title">${this.inputValue}</span>`
          );
        }
      });
    }
  }
};
</script>

<style>
.primary_title {
  background: #fff000;
}
.badge-left {
  margin-left: 5px;
}
</style>
